<template>
  <div class="box">
    <div>当前得分：{{snakeLength}}分</div>
    <div class="snake">
      <div class="row" v-for="(o) in bgList" :key="o.id">
        <!-- <span>{{o.id}}</span> -->
        <i v-show="o.isEgg" class="egg-icon"></i>
        <i v-show="o.isSnakeHead" class="snake-icon sh-icon"></i>
        <i v-show="o.isSnake" class="snake-icon"></i>
      </div>
    </div>
    <div class="flex">
      <div class="btn" @click="startMoveSnakeHead()">点击开始</div>
      <div class="btn" @click="stop">暂停</div>
    </div>
    <div class="operation">
      <div class="row">
        <i @click="listenKeyUp(38)" class="el-icon-arrow-up"></i>
        <i @click="listenKeyUp(40)" class="el-icon-arrow-down"></i>
      </div>
      <div class="col">
        <i @click="listenKeyUp(37)" class="el-icon-arrow-left"></i>
        <i @click="listenKeyUp(39)" class="el-icon-arrow-right"></i>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'snake',
  data () {
    return {
      isStart: false,
      bgList: [],
      eggId: 0,
      headId: 0,
      currentDirect: 3, // 0-上(-10);1-下(+10);2-右(+1);0-左(-1);
      setIn: {},
      snakeLength: 0,
      snakeBody: []
    }
  },
  computed: {
    speed () {
      let speed = 300
      if (this.snakeLength > 10) {
        speed = 200
      }
      if (this.snakeLength > 20) {
        speed = 100
      }
      if (this.snakeLength > 50) {
        speed = 50
      }
      return speed
    }
  },
  methods: {
    createEgg () {
      this.eggId = Math.floor(Math.random() * 400)
      const obj = this.bgList[this.eggId]
      if (obj.isSnake || obj.isSnakeHead) {
        this.createEgg()
        return false
      }
      this.bgList.map(v => {
        v.isEgg = false
      })
      this.bgList[this.eggId].isEgg = true
    },
    createSnakeHead () {
      this.headId = Math.floor(Math.random() * 400)
      if (this.bgList[this.headId].isEgg) {
        this.createSnakeHead()
      } else {
        this.bgList.map(v => {
          v.isSnakeHead = false
        })
        this.bgList[this.headId].isSnakeHead = true
      }
    },
    startMoveSnakeHead () {
      this.isStart = true
      clearInterval(this.setIn)
      // this.currentDirect = Math.floor(Math.random() * 4)
      this.moveSnakeHead()
    },
    stop () {
      this.isStart = false
      clearInterval(this.setIn)
      // this.currentDirect = Math.floor(Math.random() * 4)
      // this.moveSnakeHead()
    },
    moveSnakeHead () {
      this.setIn = setInterval(() => {
        const beforeLocal = JSON.parse(JSON.stringify(this.headId))
        const beforeData = JSON.parse(JSON.stringify(this.snakeBody))
        const dkey = this.currentDirect
        const drow = this.headId % 20
        switch (dkey) {
          case 0:
            if (drow === 0) {
              this.headId += 19
            } else {
              this.headId -= 1
            }
            break
          case 1:
            if (this.headId <= 20) {
              this.headId += 380
            } else {
              this.headId -= 20
            }
            break
          case 2:
            if (drow === 19) {
              this.headId -= drow
            } else {
              this.headId += 1
            }
            break
          case 3:
            if (this.headId >= 380) {
              this.headId = this.headId % 20
            } else {
              this.headId += 20
            }
            break
          default:
            break
        }
        if (this.bgList[this.headId].isEgg) {
          this.snakeLength++
          this.snakeBody.unshift(beforeLocal)
          this.createEgg()
        }
        if (this.snakeBody.length > 0) {
          this.snakeBody.map((v, i) => {
            if (i > 0) {
              this.$set(this.snakeBody, i, beforeData[i - 1])
            } else {
              this.$set(this.snakeBody, i, beforeLocal)
            }
          })
          this.createSnakeBody()
        }
        this.checkAndMoveHead()
      }, this.speed)
    },
    checkAndMoveHead () {
      if (this.bgList[this.headId].isSnake) {
        alert('游戏结束')
        this.snakeBody = []
        this.createSnakeBody()
        clearInterval(this.setIn)
        return false
      } else {
        this.bgList.map(v => {
          v.isSnakeHead = false
        })
        this.bgList[this.headId].isSnakeHead = true
      }
    },
    createSnakeBody () {
      this.bgList.map(v => {
        v.isSnake = false
      })
      this.snakeBody.map(v => {
        this.bgList[v].isSnake = true
      })
    },
    createBg () {
      for (let i = 0; i < 400; i++) {
        const obj = {
          id: i,
          isEgg: false,
          isSnakeHead: false,
          isSnake: false
        }
        this.bgList.push(obj)
      }
    },
    listenKeyUp (val) {
      // console.log(val)
      if (this.isStart) {
        if ((this.currentDirect === 0) && (val === 37 || val === 39)) {
          return false
        }
        if ((this.currentDirect === 3) && (val === 38 || val === 40)) {
          return false
        }
        if ((this.currentDirect === 1) && (val === 38 || val === 40)) {
          return false
        }
        if ((this.currentDirect === 2) && (val === 37 || val === 39)) {
          return false
        }
        this.currentDirect = val - 37
        clearInterval(this.setIn)
        this.moveSnakeHead()
      }
    }
  },
  created () {
    this.createBg()
    // setInterval(() => {
  },
  mounted () {
    this.createEgg()
    this.createSnakeHead()
    // }, 1000)
    // this.listenKeyUp()
  }
}
</script>

